import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import UniFormLayout from "../../components/UniFormLayout";

import Modal from "react-bootstrap/Modal";

import {
    set_email_user, selectEmail
  } from "./applicationSlice";

const schema = {
    title: '  ',
    type: 'object',
    properties: {
        email: { type: 'string' },
        user: { type: 'string' }
    },
    required: ['email', 'user'],
};

export default function LoginLogout() {
    const dispatch = useDispatch();
    const email = useSelector(selectEmail);
    const [modalShow, setModalShow] = useState(false);
    const [userEmail, setUserEmail] = useState(false);
    
    function setFormObject(objectFromUniforms) {
        setUserEmail(objectFromUniforms.email);
        dispatch(set_email_user(
            {
            email: objectFromUniforms.email,
            user: objectFromUniforms.user
          }));
    //    console.log(objectFromUniforms);
    }

    return <>
        <span onClick={() => setModalShow(true)} style={{ marginRight: "1rem" }}>{!!userEmail ? userEmail : 'anon'}</span>
        <Modal
          //  size="lg"
            show={modalShow}
            onHide={() => setModalShow(false)}
            aria-labelledby="login"
        >
            <Modal.Header closeButton>
                <Modal.Title id="login-lg"></Modal.Title>
            </Modal.Header>
            <Modal.Body>    <UniFormLayout schema={schema} setFormObject={setFormObject} /></Modal.Body>
        </Modal>
    </>
}