import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Toast from "react-bootstrap/Toast";

import UniFormLayout from "../../components/UniFormLayout";



import {
    createPost, selectPosts, mark_del_post
} from "./postsSlice";

import {
    selectSpreadsheetProtoData, load_data, new_empty_spreadsheet, set_spreadsheetTitle
 } from "../spreadsheet/spreadsheetSlice";



export default function PostsButtonGroup() {
    const dispatch = useDispatch();
    const [showSave, setShowSave] = useState(false);
    const [showOpen, setShowOpen] = useState(false);

    const handleClose = () => { setShowSave(false); setShowOpen(false) }
    const handleSaveShow = () => setShowSave(true);
    const handleOpenShow = () => setShowOpen(true);

    return <>
        <ButtonGroup aria-label="Posts Buttons" size="sm">
            <Button variant="outline-secondary"
                onClick={() => dispatch(new_empty_spreadsheet())}
                data-toggle="tooltip"
                data-placement="bottom"
                title=" "
            ></Button>
            <Button variant="outline-secondary"
                onClick={() => handleSaveShow()}
                data-toggle="tooltip"
                data-placement="bottom"
                title=" "
            ></Button>
            <Button variant="outline-secondary"
                onClick={() => handleOpenShow()}
                data-toggle="tooltip"
                data-placement="bottom"
                title=" "
            ></Button>
        </ButtonGroup>
        {showSave ? <SavePostModal show={showSave} handleClose={handleClose} /> : null}
        {showOpen ? <SelectAndOpenModal show={showOpen} handleClose={handleClose} /> : null}
    </>
}

function SavePostModal(props) {
    const dispatch = useDispatch();
    const content = useSelector(selectSpreadsheetProtoData);
    const [formDataObject, setFormDataObject] = useState({ title: '', comment: '' });
    const [editPostTitleComment, doEditPostTitleComment] = useState(true);

    function setFormObject(objectFromUniforms) {
        doEditPostTitleComment(false);
        setFormDataObject(objectFromUniforms);
    }

    const schema = {
        title: '  ',
        type: 'object',
        properties: {
            title: { type: 'string' },
            comment: { type: 'string' }
        },
        required: ['title', 'comment'],
    };

    return <Modal dialogClassName="modal-90w" show={props.show} onHide={props.handleClose}>
        <Modal.Header closeButton>
            <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            {editPostTitleComment ? <UniFormLayout schema={schema} setFormObject={setFormObject} /> :
                <blockquote className="blockquote mb-0">
                    <p>{' '}{formDataObject.title}{' '}</p>
                    <footer className="blockquote-footer">
                        <cite title={formDataObject.comment}>{formDataObject.comment}</cite>
                    </footer>
                </blockquote>
            }
        </Modal.Body>
        <Modal.Footer>
            <Button variant="outline-secondary"
                onClick={() => dispatch(createPost(
                    {
                        id: Math.floor(Math.random() * 1001),
                        title: formDataObject.title,
                        comment: formDataObject.comment,
                        type: 'spreadsheet',
                        content: content,
                        deleted: false,
                        date: new Date().toJSON()
                    }
                ))}
            ></Button>
        </Modal.Footer>
    </Modal>
}

function SelectAndOpenModal(props) {
    const dispatch = useDispatch();
    const posts = useSelector(selectPosts).posts;

    // useEffect(() => {
    //     dispatch(showPostsInBrowser())
    //   }, [])

    function closeModalopenSpreadsheet(content, title) {
        dispatch(load_data({ protoData: content }));
        dispatch(set_spreadsheetTitle({ spreadsheetTitle: title }));
        //       props.handleClose();        
    }

    return <Modal size="lg" show={props.show} onHide={props.handleClose}>
        <Modal.Header closeButton>
            <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            {!!posts && posts.length > 0 && posts.map(item =>
                <Toast key={item.id}>
                    <Toast.Header  onClick={() => closeModalopenSpreadsheet(item.content, item.title)}>
                        <svg height='20' width='20'><circle cx='10' cy='10' r='10' fill='green' /></svg>
                        {item.deleted ? <del className="mr-auto ml-2">{item.title}</del>: <span className="mr-auto ml-2">{item.title}</span>}                     
                        <small>{item.id} minuts ago</small>
                    </Toast.Header>
                    <Toast.Body>

                        {item.deleted ?
                            <svg
                                onClick={() => dispatch(mark_del_post({ id: item.id }))}
                                className="mr-3"
                                height='20' width='20'><circle cx='10' cy='10' r='10' fill='red' /></svg>
                            :
                            <svg
                                onClick={() => dispatch(mark_del_post({ id: item.id }))}
                                className="mr-3"
                                height='20' width='20'><circle cx='10' cy='10' r='9' stroke="grey" strokeWidth="1" fill="white" /></svg>
                        }
                        <span>{item.deleted ? '  ,      ' : item.comment }</span>

                    </Toast.Body>
                </Toast>
            )}

        </Modal.Body>
        {/* <Modal.Footer>
               <Button variant="outline-secondary" onClick={() => props.handleClose()}>S</Button>
        </Modal.Footer> */}
    </Modal>
}